:root {
  --light: #f0f0f0;
  --primary: #292b33;
  --secondary: #d8d7d4;
  --red-color: #ed213a;
}

@font-face {
  font-family: "Game_Of_Squids";
  src: url("/the-masterpiece.fr/static/media/Game Of Squids.e0727d0b.ttf");
}

@font-face {
  font-family: "Zamaica";
  src: url(/the-masterpiece.fr/static/media/Zamaica.467618a3.ttf);
}
@font-face {
  font-family: "futural";
  src: url(/the-masterpiece.fr/static/media/futural.4a4ab345.ttf);
}
@font-face {
  font-family: "futurali";
  src: url(/the-masterpiece.fr/static/media/futurali.793b2481.ttf);
}

a {
  text-decoration: none;
}
.text-red-color {
  color: #ed213a;
  color: var(--red-color);
}
.text-primary-color {
  color: #292b33;
  color: var(--primary);
}
.text-red-color:hover {
  color: #ed213a;
  color: var(--red-color);
}
.w-80 {
  width: 80%;
}
.w-60 {
  width: 60%;
}
.w-40 {
  width: 40%;
}
.w-30 {
  width: 30%;
}
.w-20 {
  width: 20%;
}
.positionTopPage {
  padding-top: 8rem;
}
@media (min-width: 767.98px) {
  .positionTopPage {
    min-height: calc(100vh - 133px);
  }
}
@media (max-width: 767.98px) {
  .positionTopPage {
    padding-top: 3rem;
  }
}
.border-red {
  border-color: #ed213a !important;
  border-color: var(--red-color) !important;
}

:root {
  --spacingIcon: 50px;
}
.styleNavbar ul {
  list-style-type: none;
}
.linkNavbar {
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 5px 7px;
  border-radius: 10px 10px 0px 0px;
  position: relative;
  transform: translateY(-0.3rem);
  transition: 0.5s;
  color: var(--red-color);
}
.activeIcon {
  transform: translateY(0.1rem);
  background-color: white;
  color: black;
}
.activeIconMobile {
  transform: translateY(-0.1rem);
  background-color: white;
  color: black;
}
a:hover {
  color: black;
  background-color: white;
}
@media (max-width: 767.98px) {
  a:hover {
    color: black;
  }
}

@font-face {
  font-family: "MajorMonoDisplay-Regular";
  src: url(/the-masterpiece.fr/static/media/MajorMonoDisplay-Regular.c38f17b4.ttf);
}
@font-face {
  font-family: "olfsimplesansoc-regular";
  src: url(/the-masterpiece.fr/static/media/olfsimplesansoc-regular.e9b08d41.ttf);
}
@font-face {
  font-family: "Roblox";
  src: url("/the-masterpiece.fr/static/media/Roblox Font Light.c76fb4dd.ttf");
}

.title {
  font-family: "MajorMonoDisplay-Regular";
}
.styleM {
  font-family: "olfsimplesansoc-regular";
}
.styleP {
  font-family: "Roblox";
}

.animated-button1 {
  height: 70px;
  width: 55px;
  display: inline-block;
  transform: translate(0%, 0%);
  overflow: hidden;
}
.animated-button1 span {
  position: absolute;
}

.animated-button1 span:nth-child(1) {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 4px;
  background: linear-gradient(to left, var(--primary), var(--red-color));
  animation: 2s animateTop linear infinite;
}

@keyframes animateTop {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.animated-button1 span:nth-child(2) {
  top: 0px;
  right: 0px;
  height: 100%;
  width: 4px;
  background: linear-gradient(to top, var(--primary), var(--red-color));
  animation: 2s animateRight linear -1s infinite;
}

@keyframes animateRight {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(-100%);
  }
}

.animated-button1 span:nth-child(3) {
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 4px;
  background: linear-gradient(to right, var(--primary), var(--red-color));
  animation: 2s animateBottom linear infinite;
}

@keyframes animateBottom {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.animated-button1 span:nth-child(4) {
  top: 0px;
  left: 0px;
  height: 100%;
  width: 4px;
  background: linear-gradient(to bottom, var(--primary), var(--red-color));
  animation: 2s animateLeft linear -1s infinite;
}

@keyframes animateLeft {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(100%);
  }
}

.main {
  height: 100vh !important;
}
video {
  position: absolute;
  left: auto;
}

.textFirstSectionHomeTitle {
  font-family: "futurali", sans-serif;
}
.textFirstSectionHomeP {
  font-family: "futural", sans-serif;
}

.form-control,
.form-select {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  border-radius: 6px;
}

.linkSocial > a:hover {
  background-color: transparent;
  color: none;
}

.textContact {
  font-weight: bold;
  font-family: "Srisakdi";
}

